.index-item {
	position: relative;
	display: flex!important;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;
	box-sizing: border-box;
	padding: 20px;
	text-align: center;
	color: $text-light;
	text-decoration: none;
	overflow: hidden;
	transition: .25s;
	&::after,
	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: linear-gradient(to bottom, transparent, transparent, $marine);
		transition: .25s; }
	&::before {
		z-index: 1;
		background-image: linear-gradient(to bottom, transparent, transparent, darken($marine, 5));
		opacity: 0; }
	&:hover {
		color: $text-light;
		text-decoration: none; }
	&_banner {
		justify-content: center;
		@include r(992) {
			width: 100%!important; }
		&::before,
		&::after {
			display: none; } }
	&-banner {
		position: absolute;
		top: 20px;
		right: 20px;
		z-index: 2;
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: $lato;
		@include fs(18);
		font-weight: 800;
		font-style: italic;
		color: $text-light;
		&::before,
		&::after {
			content: '';
			display: inline-block;
			width: 22px;
			height: 1px;
			background-color: $text-light; }
		&::before {
			margin-right: 5px; }
		&::after {
			margin-left: 5px; } }
	&__banner {
		max-width: 100%;
		max-height: 100%;
		width: auto;
		height: auto; }
	& > *:not(.index-item__img):not(.index-item-cost):not(.index-item-banner) {
		position: relative;
		z-index: 5; }
	&__img {
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 0;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		transform: translate(-50%, -50%); }
	&-cost {
		position: absolute;
		top: 20px;
		right: 20px;
		z-index: 2;
		min-width: 112px;
		min-height: 112px;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		@include r(1400) {
			min-width: 105px;
			min-height: 105px; }
		@include r(1200) {
			min-width: 100px;
			min-height: 100px; }
		@include r(1000) {
			min-width: 90px;
			min-height: 90px; }
		.icon {
			position: absolute;
			top: 0;
			right: 0;
			box-sizing: border-box;
			width: 100%;
			height: 100%;
			fill: $marine;
			stroke: $text-light;
			animation: rotate 10s linear infinite;
			animation-play-state: paused;
			transition: .25s; }
		&:hover {
			.icon {
				animation-play-state: running; } }
		&__text {
			position: relative;
			z-index: 5;
			font-family: $lato;
			@include fs(22);
			@include lh(22, 22);
			@include ls(4);
			font-weight: 800;
			font-style: italic;
			color: $text-light;
			@include r(1400) {
				@include fs(20); }
			@include r(1200) {
				@include fs(18); }
			@include r(1000) {
				@include fs(16); } } }
	&__title,
	&__text {
		margin: 0;
		text-align: center;
		font-family: $lato;
		@include fs(22);
		@include lh(22, 30);
		font-weight: 800;
		font-style: italic;
		color: $text-light;
		@include r(1200) {
			@include fs(20); }
		@include r(1000) {
			@include fs(18); } } }


.slick-current {
	.index-item {
		@include rmin(450) {
			border-right: 3px solid $text-light; } } }

.index-items-slider-four {
	border-top: 3px solid $text-light;
	.slick-current + .slick-active + .slick-active + .slick-active {
		.index-item {
			border-right: 0; } }
	@include r(992) {
		display: none; } }


@keyframes rotate {
	100% {
		transform: rotate(360deg); } }
